import Button from '../Button';
import Input from '../Input';
import propTypes from 'prop-types';
import { useState } from 'react';

function Search(props) {
  const [ query, setQuery ] = useState('');

  const handleQueryChange = (e) => {
    setQuery(e.target.value);
  }

  return (
    <div className="row">
      <div className="col-6 mr-0 pr-0">
        <Input type="search" onChange={ handleQueryChange } />
      </div>
      <div className="col-3 ml-0 p-0">
        <Button label="Search" className="btn btn-secondary" onClick={ () => props.performQuery(query) } />
      </div>
    </div>
  );
}

Search.propTypes = {
  performQuery: propTypes.func,
}

export default Search;