import propTypes from 'prop-types';
import timeConvert from '../../helper/time';
import { useEffect, useState } from 'react';

function filterAuthors(authorIds, authors) {
  return authorIds.map(authorId => {
    return <span className="d-block" key={ authorId }>{ authors.find(author => author.id === authorId).name }</span>;
  });
}

async function fetchCourse(courseId) {
  const url = 'https://courses-be.herokuapp.com/courses/' + courseId;
  const response = await fetch(url, {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json'
    },
  });
  return response.json();
}

async function fetchAuthors() {
  const url = 'https://courses-be.herokuapp.com/authors/all';
  const response = await fetch(url, {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json'
    },
  });
  return response.json();
}

function CourseInfo(props) {
  const authorStyle = {
    maxWidth: 400
  }
  const [course, setCourse] = useState({});
  const [authors, setAuthors] = useState({});

  useEffect(() => {
    fetchCourse(props.match.params.courseId).then(data => {
      setCourse(data.result);
    });
    fetchAuthors().then(data => {
      setAuthors(data.result);
    });
  }, [props]);

  function renderCourse(course, authors) {
    const authorMap = filterAuthors(course.authors, authors);
    return (
      <>
      <div className="d-flex">
        <div className="p-2 w-50 flex-grow-1">
          <a href="/courses">&lt; Back to Courses</a>
        </div>
      </div>
      <div className="d-flex border-bottom pb-4 m-2" id={ course.id }>
        <div className="p-2 w-75">
          <h2>{ course.title }</h2>
          <p className="text-secondary">{ course.description }</p>
        </div>
        <div className="p-2 w-50">
          <p><strong>ID:</strong> <span className="text-secondary">{ course.id }</span></p>
          <p><strong>Duration:</strong> <span className="text-secondary" title={ course.duration + ' minutes'}>{ timeConvert(course.duration) }</span></p>
          <p><strong>Created:</strong> <span className="text-secondary">{ course.creationDate }</span></p>
          <p><strong>Authors:</strong> <span className="text-truncate text-secondary" style={ authorStyle } >{ authorMap }</span></p>
        </div>
      </div>
      </>
    );
  }

  return (
    <>
      {!course.id || !authors.length ? <h1>Loading</h1> : renderCourse(course, authors) }
    </>
  );
}

CourseInfo.propTypes = {
  history: propTypes.object,
  location: propTypes.object,
  match: propTypes.object,
  staticContext: propTypes.any,
};

export default CourseInfo;