import CourseCard from '../CourseCard';
import Search from '../Search';
import { useEffect, useState } from 'react';

async function fetchCourses() {
  const url = 'https://courses-be.herokuapp.com/courses/all';
  const response = await fetch(url, {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json'
    },
  });
  return response.json();
}

async function filterCourses(query) {
  const url = 'https://courses-be.herokuapp.com/courses/filter?title=' + query;
  const response = await fetch(url, {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json'
    },
  });
  return response.json();
}

async function fetchAuthors() {
  const url = 'https://courses-be.herokuapp.com/authors/all';
  const response = await fetch(url, {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json'
    },
  });
  return response.json();
}

function Courses() {
  const [courses, setCourses] = useState({});
  const [authors, setAuthors] = useState({});

  useEffect(() => {
    fetchCourses().then(data => {
      setCourses(data.result);
    });
    fetchAuthors().then(data => {
      setAuthors(data.result);
    });
  }, []);

  function renderCourses(courses, authors) {
    return courses.map((course) => {
      return <CourseCard
        course={ course }
        authors={ authors }
        key={ course.id }
      />
    });
  }

  function handleSearch(query) {
    if (query) {
      filterCourses(query).then(data => {
        setCourses(data.result);
      });
    } else {
      fetchCourses().then(data => {
        setCourses(data.result);
      })
    }
  }
  
  return (
    <>
      <div className="d-flex">
        <div className="p-2 w-50 flex-grow-1">
          <Search performQuery={ handleSearch } />
        </div>
        <div className="d-flex align-items-end">
          <div className="p-2">
            <a href="/courses/add" className="btn btn-primary">Add new course</a>
          </div>
        </div>
      </div>
      { !courses.length || !authors.length ? <h1>Loading</h1> : renderCourses(courses, authors) }
    </>
  );
}

export default Courses;