import Button from '../Button';
import Input from '../Input';
import propTypes from 'prop-types';
import { useInput } from '../../hooks/useInput';
import { useState } from 'react';

async function postUser(data={}) {
  const url = 'https://courses-be.herokuapp.com/login';
  const response = await fetch(url, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(data)
  });
  return response.json();
}

function Login(props) {
  const { value:email, bind:bindEmail } = useInput('');
  const { value:password, bind:bindPassword } = useInput('');
  const [ loginError, setLoginError ] = useState(false);

  const handleSubmit = (e) => {
    if (!e.target.form.checkValidity()) {
      e.target.form.classList.add('was-validated');
      return;
    }

    e.preventDefault();
    
    postUser({'email': email, 'password': password}).then(data => {
      if (data.successful === true) {
        props.handleLogin(data);
      } else {
        setLoginError(true);
      }
    });
  };
  
  return (
    <div className="d-flex justify-content-center">
      <div className="w-50">
        <form method="post" onSubmit={ handleSubmit }>
          <div className="mb-4">
            { loginError === true ? <div className="alert alert-danger">Either your address cannot found or you provided an invalid password!</div> : '' }
            <Input label="Email" type="email" placeholder="Enter email" required autoFocus={ true } {...bindEmail} />
            <div className="invalid-feedback">Please provide a valid email address!</div>
          </div>
          <div className="mb-4">
            <Input label="Password" type="password" placeholder="Enter password" required {...bindPassword} />
            <div className="invalid-feedback">Please fill password field!</div>
          </div>
          <div className="mb-4">
            <Button type="submit" label="Login" className="btn btn-primary" onClick={ handleSubmit } />
          </div>
          <p>If you don't have an account, you can <a href="/registration">register here</a>.</p>
        </form>
      </div>
    </div>
  );
}

Login.propTypes = {
  handleLogin: propTypes.func,
};

export default Login;