import Button from '../Button';
import Input from '../Input';
import timeConvert from '../../helper/time';
import { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { useInput } from '../../hooks/useInput';

async function fetchAuthors() {
  const url = 'https://courses-be.herokuapp.com/authors/all';
  const response = await fetch(url, {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json',
    },
  });
  return response.json();
}

async function createAuthor(data, token) {
  const url = 'https://courses-be.herokuapp.com/authors/add';
  const response = await fetch(url, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
      'Authorization': token,
    },
    body: JSON.stringify(data)
  });
  return response.json();
}

async function saveCourse(data, token) {
  const url = 'https://courses-be.herokuapp.com/courses/add';
  const response = await fetch(url, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
      'Authorization': token,
    },
    body: JSON.stringify(data)
  });
  return response.json();
}

function CreateCourse() {
  const history = useHistory();
  const [ authors, setAuthors ] = useState({});
  const { value:newAuthorName, bind:bindNewAuthorName, reset:resetNewAuthorName } = useInput('');
  const { value:title, bind:bindTitle } = useInput('');
  const { value:description, bind:bindDescription } = useInput('');
  const { value:duration, bind:bindDuration } = useInput('');
  const [ selectedAuthors, setSelectedAuthors ] = useState([]);

  useEffect(() => {
    fetchAuthors().then(data => {
      setAuthors(data.result);
    });
  }, []);

  const handleSaveAuthor = () => {
    const data = JSON.parse(localStorage.getItem('session'));
    if (data && data['result'] !== undefined) {
      createAuthor({name: newAuthorName}, data['result']).then(data => {
        if (data['successful'] === true) {
          resetNewAuthorName();
          fetchAuthors().then(data => {
            setAuthors(data.result);
          });
        }
      });
    }
  };

  const handleAuthorSelect = (e) => {
    setSelectedAuthors([e.target.dataset.author, ...selectedAuthors]);
  };

  const handleAuthorRemove = (e) => {
    setSelectedAuthors(selectedAuthors.filter(authorId => { return authorId !== e.target.dataset.author }));
  };

  const handleSaveCourse = (e) => {
    const data = JSON.parse(localStorage.getItem('session'));
    if (data && data['result'] !== undefined) {
      saveCourse({title: title, description: description, duration: parseInt(duration), authors: selectedAuthors}, data['result']).then(data => {
        if (data['successful'] === true) {
          history.push('/courses');
        }
      });
    }
  };

  const handleCancel = () => {
    history.push('/courses');
  };

  const listAvailableAuthors = (authors, selectedAuthors) => {
    selectedAuthors = selectedAuthors || [];
    let availableAuthors = authors.filter(author => 
      !selectedAuthors.some(selected => selected === author.id)
    );
    return availableAuthors.map((author) => {
      return(
        <div className="d-flex w-50 align-items-center" key={ author.id }>
          <div className="d-flex p-2 w-75">{ author.name }</div>
          <div className="d-flex p-2">
            <Button label="Add author" className="btn btn-success btn-sm m-2" data-author={ author.id } onClick={ handleAuthorSelect } />
          </div>
        </div>
      );
    });
  };

  const listSelectedAuthors = (authorIds, authors) => {
    authorIds = authorIds || [];

    if (authorIds.length === 0) {
      return 'Author list is empty';
    }

    return authorIds.map(authorId => {
      let author = authors.find(author => author.id === authorId).name;
      return (
        <div className="d-flex w-50 align-items-center" key={ authorId }>
          <div className="p-2 w-75">{ author }</div>
          <div className="p-2">
            <Button label="Remove author" className="btn btn-danger btn-sm m-2" data-author={ authorId } onClick={ handleAuthorRemove } />
          </div>
        </div>
      );
    });
  };

  function renderEditor(authors) {
    return (
      <>
        <div className="d-flex">
          <div className="p-2 w-50 flex-grow-1">
            <Input label="Title" placeholder="Enter title..." {...bindTitle} />
          </div>
          <div className="d-flex align-items-end">
            <div className="p-2">
              <Button label="Create course" className="btn btn-primary" onClick={ handleSaveCourse } />
              <Button label="Cancel" className="btn btn-link" onClick={ handleCancel } />
            </div>
          </div>
        </div>
        <div className="d-flex">
          <div className="p-2 w-100">
            <Input label="Description" placeholder="Enter description" type="textarea" {...bindDescription} />
          </div>
        </div>
        <div className="d-flex">
          <div className="p-2 w-50 flex-grow-1">
            <div className="create_course--author">
              <h3 className="text-center">Add Author</h3>
              <div className="d-flex">
                <div className="w-50 flex-grow-1 pr-2">
                  <span className="align-bottom">
                  <Input label="Author name" placeholder="Enter author name..." {...bindNewAuthorName} />
                  </span>
                </div>
                <div className="d-flex align-items-end">
                  <div className="pl-2">
                    <Button label="Create author" className="btn btn-secondary" onClick={ handleSaveAuthor } />
                  </div>
                </div>
              </div>
              <h3 className="text-center">Duration</h3> 
              <Input label="Duration" className="form-control mb-5" placeholder="Enter duration in minutes..." {...bindDuration} />
              <h3>Duration: <strong>{ timeConvert(duration) }</strong> hours</h3>
            </div>
          </div>
          <div className="p-2 w-50">
            <div className="create_course--author_list">
              <h3 className="text-center">Course authors</h3>
              <div className="d-flex flex-column align-items-center">
                { listAvailableAuthors(authors, selectedAuthors) }
              </div>
              <h3 className="text-center">Course authors</h3>
              <div className="d-flex flex-column align-items-center">
                { listSelectedAuthors(selectedAuthors, authors) }
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }

  return (
    <>
      {!authors.length ? <h1>Loading...</h1> : renderEditor(authors) }
    </>
  );
}

export default CreateCourse;