import Button from '../Button';
import Input from '../Input';
import { useHistory } from 'react-router-dom';
import { useInput } from '../../hooks/useInput';

async function postUser(url, data={}) {
  const response = await fetch(url, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(data)
  });
  return response.json();
}

function Registration() {
  const history = useHistory();
  const { value:name, bind:bindName } = useInput('');
  const { value:email, bind:bindEmail } = useInput('');
  const { value:password, bind:bindPassword } = useInput('');

  const handleSubmit = (e) => {
    if (!e.target.form.checkValidity()) {
      e.target.form.classList.add('was-validated');
      return;
    }
    e.preventDefault();
    const endPoint = 'https://courses-be.herokuapp.com/register';
    postUser(endPoint, {'name': name, 'email': email, 'password': password}).then(data => {
      if (data.successful === true) {
        history.push('/login');
      }
    })
  }
  
  return (
    <div className="d-flex justify-content-center">
      <div className="w-50">
        <form method="post" onSubmit={ handleSubmit }>
          <div className="mb-4">
            <Input label="Name" type="text" placeholder="Enter name" required autoFocus={ true } {...bindName} />
            <div className="invalid-feedback">Please fill name field!</div>
          </div>
          <div className="mb-4">
            <Input label="Email" type="email" placeholder="Enter email" required {...bindEmail} />
            <div className="invalid-feedback">Please provide a valid email address!</div>
          </div>
          <div className="mb-4">
            <Input label="Password" type="password" placeholder="Enter password" required {...bindPassword} />
            <div className="invalid-feedback">Please fill password field!</div>
          </div>
          <div className="mb-4">
            <Button label="Register" className="btn btn-primary" onClick={ handleSubmit } />
          </div>
          <p>If you have an account, you can <a href="/login">login here</a>.</p>
        </form>
      </div>
    </div>
  );
}

export default Registration;