import CourseInfo from './components/CourseInfo';
import Courses from './components/Courses';
import CreateCourse from './components/CreateCourse';
import Header from './components/Header';
import './App.css';
import React, { useEffect, useState } from 'react';
import {
  Route,
  Switch,
  useHistory,
  useLocation
} from "react-router-dom";
import Login from './components/Login';
import Registration from './components/Registration';

function App() {
  const history = useHistory();
  const location = useLocation();
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [session, setSession] = useState({});

  async function authUser(token) {
    const url = 'https://courses-be.herokuapp.com/users/me';
    const response = await fetch(url, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': token,
      },
    });
    return response.json();
  }

  useEffect(() => {
    if (isLoggedIn === false) {
      let localSession = JSON.parse(localStorage.getItem('session'));
      if (localSession && localSession['result'] !== undefined) {
        authUser(localSession['result']).then(data => {
          if (data.successful === true) {
            setSession({name: data.result.name, email: data.result.email});
            setIsLoggedIn(true);
          }
        });
      }
    }
  }, [location, isLoggedIn]);

  const initSession = (session) => {
    localStorage.setItem('session', JSON.stringify(session));
    setSession(session.user);
    history.push('/courses');
  };

  const destroySession = () => {
    localStorage.removeItem('session');
    setSession({});
    setIsLoggedIn(false);
    history.push('/login');
  };
  return (
    isLoggedIn === false ?
    <>
      <Header />
      { location.pathname !== '/registration' ? <Login handleLogin={ initSession } /> : <Registration /> }
    </> :
    <>
      <Header logout={ destroySession } user={ session.name || session.email } />
      <Switch>
        <Route path="/courses" exact component={ Courses } />
        <Route path="/courses/add" exact component={ CreateCourse } />
        <Route path="/courses/:courseId" component={ CourseInfo } />
        <Route path="/login">
          <Login session={ session } />
        </Route>
      </Switch>
    </>
  );
}

export default App;
